export const CHANGE_THEME = 'CHANGE_THEME';
export const TOGGLE_BOXED_LAYOUT = 'TOGGLE_BOXED_LAYOUT';

export const changeTheme = (theme_name) => ({
  type: CHANGE_THEME,
  payload: theme_name,
});

export const switchBoxedLayout = (flag) => ({
  type: TOGGLE_BOXED_LAYOUT,
  payload: flag,
});



