// StringEditable
export const STRING_OPTIONS = {
  disableReturn: true,
  placeholder: {
    text: '  -..',
    hideOnClick: true,
  },
  paste: {
    forcePlainText: true,
    cleanPastedHTML: true,
    cleanAttrs: ['class', 'style', 'dir'],
    cleanTags: ['meta'],
  },
  toolbar: false,
};

export const BUTTON_OPTIONS = {
  disableReturn: true,
  placeholder: {
    text: '',
    hideOnClick: true,
  },
  paste: {
    forcePlainText: true,
    cleanPastedHTML: true,
    cleanAttrs: ['class', 'style', 'dir'],
    cleanTags: ['meta'],
  },
  toolbar: false,
}

export const RICH_OPTIONS = {
  paste: {
    cleanPastedHTML: true,
    cleanAttrs: ['class', 'style', 'dir'],
    cleanTags: ['meta'],
  },
  placeholder: {
    text: '  -..',
    hideOnClick: true,
  },
  toolbar: {
    allowMultiParagraphSelection: false,
    // TODO make static on top with full width
    static: true,
    sticky: true,
  },
};

export default {
  string: STRING_OPTIONS,
  rich: RICH_OPTIONS,
  button: BUTTON_OPTIONS,
};



